import React, { useEffect, useState } from 'react';
import { useSearchParams } from 'react-router-dom';
import { CheckCircle, AlertCircle, Loader2, Mail } from 'lucide-react';
import { toast } from 'sonner';

const GmailPopupCallback: React.FC = () => {
  const [searchParams] = useSearchParams();
  const [status, setStatus] = useState<'loading' | 'success' | 'error'>('loading');
  const [message, setMessage] = useState('Processing Gmail authentication...');

  useEffect(() => {
    const handleCallback = async () => {
      try {
        const success = searchParams.get('success');
        const email = searchParams.get('email');
        const error = searchParams.get('error');

        if (success === 'true' && email) {
          setStatus('success');
          setMessage('Gmail account connected successfully!');

          console.log('ðŸŽ‰ Gmail OAuth success in popup, notifying parent window');

          // Notify parent window of success
          if (window.opener && !window.opener.closed) {
            console.log('ðŸ“¤ Sending success message to parent window');
            window.opener.postMessage({
              type: 'GMAIL_AUTH_SUCCESS',
              data: {
                email: decodeURIComponent(email),
                isConnected: true
              }
            }, window.location.origin);

            // Close popup after a short delay to allow parent to process
            setTimeout(() => {
              console.log('ðŸ”’ Closing popup window');
              window.close();
            }, 1500);
          } else {
            console.log('âš ï¸ No parent window found, closing popup');
            // If no opener, try to close anyway
            setTimeout(() => {
              window.close();
            }, 2000);
          }
        } else if (error) {
          setStatus('error');
          setMessage(`Authentication failed: ${decodeURIComponent(error)}`);
          
          // Notify parent window of error
          if (window.opener && !window.opener.closed) {
            window.opener.postMessage({
              type: 'GMAIL_AUTH_ERROR',
              message: decodeURIComponent(error)
            }, window.location.origin);
            
            // Close popup after a short delay
            setTimeout(() => {
              window.close();
            }, 3000);
          } else {
            // If no opener, try to close anyway
            setTimeout(() => {
              window.close();
            }, 3000);
          }
        } else {
          setStatus('error');
          setMessage('Invalid callback parameters');
          
          // Close popup after error
          setTimeout(() => {
            window.close();
          }, 3000);
        }
      } catch (error) {
        console.error('Error handling Gmail popup callback:', error);
        setStatus('error');
        setMessage('An unexpected error occurred');
        
        // Close popup after error
        setTimeout(() => {
          window.close();
        }, 3000);
      }
    };

    handleCallback();
  }, [searchParams]);

  return (
    <div className="min-h-screen bg-gray-50 flex items-center justify-center p-4">
      <div className="max-w-md w-full bg-white rounded-lg shadow-lg p-6">
        {status === 'loading' && (
          <div className="text-center space-y-4">
            <div className="flex justify-center">
              <div className="relative">
                <div className="w-16 h-16 bg-blue-100 rounded-full flex items-center justify-center">
                  <Mail className="h-8 w-8 text-blue-600" />
                </div>
                <div className="absolute -top-1 -right-1 w-6 h-6 bg-blue-500 rounded-full flex items-center justify-center">
                  <Loader2 className="h-3 w-3 text-white animate-spin" />
                </div>
              </div>
            </div>
            <div className="space-y-2">
              <h3 className="text-lg font-semibold text-gray-900">
                Processing Authentication
              </h3>
              <p className="text-sm text-gray-600">
                {message}
              </p>
            </div>
          </div>
        )}

        {status === 'success' && (
          <div className="text-center space-y-4">
            <div className="flex justify-center">
              <CheckCircle className="h-16 w-16 text-green-600" />
            </div>
            <div className="space-y-2">
              <h3 className="text-lg font-semibold text-green-900">
                Successfully Connected!
              </h3>
              <p className="text-sm text-green-700">
                {message}
              </p>
              <p className="text-xs text-gray-500">
                This window will close automatically...
              </p>
            </div>
          </div>
        )}

        {status === 'error' && (
          <div className="text-center space-y-4">
            <div className="flex justify-center">
              <AlertCircle className="h-16 w-16 text-red-600" />
            </div>
            <div className="space-y-2">
              <h3 className="text-lg font-semibold text-red-900">
                Connection Failed
              </h3>
              <p className="text-sm text-red-700">
                {message}
              </p>
              <p className="text-xs text-gray-500">
                This window will close automatically...
              </p>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default GmailPopupCallback;
